.containerSwiper{
    max-width: 1200px;
}
.containerCards{
    max-width: 1024px;
    padding: 0 24px;
}
.container{
    width: 288px;
    height: 320px;
    overflow: hidden;
    position: relative;
    border-radius: 12px;
    background: radial-gradient(50% 50% at 50% 50%, #AAD5FE 0%, #E5F2FE 100%);
}
.containerImagem{
    height: 60%;
    overflow: hidden;
}
.container img{
    width: 100%;
}
.icon{
    position: absolute;
    padding: 26px;
    background: #FFF;
    border: 4px solid #AAD5FE;
    border-radius: 80px;
    bottom: 80px;
    left: 90px;
    font-size: 48px;
}
.containerInfo{
    flex: 1;
}
.descricao{
    display: flex;
    justify-content: center;
    align-items: flex-end;
    padding: 16px 8px;
    height: 110px;
}
.descricao p{
    font-family: 'Baloo 2';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 25px;
    text-align: center;
    color: #11314F;
}