.container{
    display: flex;
    flex-direction: column;
    gap: 16px;
    justify-content: center;
    align-items: center;
}
.ultimosArtigos{
    max-width: 1200px;
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: center;
    gap: 16px;
}
.containerCards{
    padding: 16px 24px;
    cursor: pointer;
}
.noticiaInfo{
    display: flex;
    gap: 16px;
    align-items: center;
}
.card{
    display: flex;
    width: fit-content;
    gap: 16px;
    align-items: center;
    background: #FFFFFF;
    box-shadow: 0px 1px 4px rgba(14, 31, 53, 0.12), 0px 4px 8px rgba(14, 31, 53, 0.08);
    border-radius: 22px;
    padding: 16px;
}
.card h1{
    max-width: 350px;
    font-family: 'Baloo 2';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 25px;
    color: #11314F;
}
.card p{
    max-width: 350px;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 300;
    font-size: 14px;
    line-height: 21px;
    color: #11314F;
}
.containerImagem{
    display: flex;
    align-items: center;
    width: 120px;
    height: 120px;
    overflow: hidden;
}
.containerImagem img{
    width: 100%;
    border-radius: 22px;
}
.containerBotao{
    width: 200px;
}