.container{
    max-width: 1200px;
    height: 400px;
    border-radius: 22px;
}
.slideLimitador{
    width: 100%;
    padding: 0 24px;
}
.imagemContainer{
    flex: 4;
    display: flex;
    align-items: center;
    overflow: hidden;
}
.imagemContainer img{
    width: 100%;
}
.slide{
    display: flex;
}
.postInfo{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 16px;
    flex: 2;
    padding: 24px;
    background: #E5F2FE;
}
.postInfo article{
    display: flex;
    flex-direction: column;
    gap: 16px;
}
.postInfo h1{
    font-family: 'Baloo 2', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 97.9%;
    text-align: center;
    color: #359EFE;
}
.postInfo p{
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: justify;
    padding: 0 24px;
}
.containerDots{
    width: 100%;
    justify-content: center;
    display: flex;
    gap: 8px;
    padding: 16px;
}
.dots{
    width: 16px;
    height: 16px;
    background: #70B9FE;
}
.dotAtivo{
    width: 16px;
    height: 16px;
    border: 2px solid #70B9FE;
}