.container{
    display: flex;
    flex-direction: column;
    padding: 16px 0px 8px 0px;
}
.container h1{
    font-family: 'Baloo 2';
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    text-align: center;
    color: #359EFE;
    text-shadow: 0px 0px 4px #FFFFFF;
    text-transform: uppercase;
}
.container h2{
    font-family: 'Baloo 2';
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    text-align: center;
    color: #FEC770;
    text-shadow: 0px 0px 4px #FFFFFF;
}