button{
    background-color: unset;
    border-style: none;
    border-radius: 22px;
    font-family: 'Baloo 2', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    text-transform: uppercase;
    cursor: pointer;
}

a{
    text-transform: uppercase;
    cursor: pointer;
    padding: 16px 8px;
    border-style: none;
    border-radius: 22px;
    font-family: 'Baloo 2', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    text-decoration: none;
    display: flex;
    justify-content: center;
}

.primario{
    background: #FFB336;
    color: #FFF;
}
.secundario{
    color: #FFB336;
    border: 2px solid #FFB336;
}
.primario button{
    color: #FFF;
}
.secundario button{
    color: #FFB336;
}